.sidebarUser {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.userList {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
}

h2 {
  margin: 0;
  text-align: center;
  font-size: 28px;
  font-weight: 600;
  color: grey;
}

.userName {
  width: 100%;
  height: 30px;
  background-color: white;
  margin-top: 7px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5%;
  box-shadow: 2px 2px 5px rgb(124, 140, 145);
  cursor: pointer;
}

.userName > a {
  text-decoration: none;
  color: black;
}
